import { createStore } from 'vuex'

export default createStore({
  state: {
    accounts: [{
      username: "admin",
      password: "letrunghieu"
    }, {
      username: "adminx",
      password: "hihihi"
    }, {
      username: "admin22",
      password: "lthchinchin"
    }],
    userInfo: {

    }
  },
  getters: {
    isLogged(state) {
      return state.userInfo.username ? true : false
    },
    getUserInfo(state) {
      return state.userInfo.username
    }
  },
  mutations: {
    checkLogin(state, payload) {
      // console.log(payload)
      state.accounts.filter(val => val.username === payload.username && val.password === payload.password).length > 0 ? state.userInfo = payload : ''
    },
    logout(state) {
      state.userInfo = {}
    }
  },
  actions: {
    checkLogin({ commit }, payload) {
      commit('checkLogin', payload)
    },
    logout({ commit }) {
      commit('logout')
    }
  },
  modules: {
  }
})
