import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue'
import Login from '../views/Login.vue'
import Store from '../store'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/login',
    name: 'Login',
    component: Login,
    beforeEnter: (to, from, next) => {
      if (Store.getters.isLogged == false) {
        next()
      }
    }
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/dogs',
    name: 'Dogs',
    // route level code-splitting
    // this generates a separate chunk (Dogs.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/Dogs.vue')
  },
  {
    path: '/dog/:dogId',
    name: 'dog',
    component: () => import(/* webpackChunkName: "about" */ '../views/DetaiDog.vue')
  },
  {
    path: '/vee',
    name: 'vee',
    component: () => import(/* webpackChunkName: "about" */ '../views/Vee.vue')
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

router.beforeEach((to, from, next) => {
  // console.log(to.name);
  // console.log(Store.getters.isLogged);
  if (to.name !== 'Login' && Store.getters.isLogged === false) next({ name: 'Login' })
  else next()
})


export default router
