<template>
  <div class="form-submit">
    <h4 class="title">
      <slot>Submit Form</slot>
    </h4>
    <div class="form-content">
      <div class="form-inp">
        <label for="username">UserName:</label>
        <input type="text" id="username" v-model="formData.username" />
      </div>
      <div class="form-inp">
        <label for="password">PassWord:</label>
        <input type="password" id="password" v-model="formData.password" />
      </div>
      <div class="form-inp">
        <button style="margin-left: auto !important" @click="submitForm">
          Login
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      formData: {
        username: "",
        password: "",
      },
    };
  },
  methods: {
    submitForm() {
      this.$store.dispatch("checkLogin", this.formData);
      this.$router.push({ path: "dogs" });
    },
  },
};
</script>

<style scoped>
.form-submit {
  padding: 30px;
  border: 1px solid rgb(226, 226, 226);
  border-radius: 5px;
  margin: 0 auto;
  width: 100%;
  max-width: 350px;
}
.form-inp {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
  margin: 10px auto;
}
label {
  margin-right: 15px;
}
</style>